<template>
  <div class="bg">
    <van-nav-bar title="充值" left-arrow @click-left="$router.go(-1)" />
    <div class="box">
      <h3>充值金额</h3>
      <div class="prive">
        <span>￥</span>
        <input type="text" v-model="prive" @click="active=true" @keyup="prohibit" />
      </div>
    </div>
    <div class="box-btn">
      <van-button
        v-show="!prive"
        class="btn-Submi"
        disabled
        type="primary"
        size="small"
        color="#1989fa"
      >下一步</van-button>

      <van-button
        v-show="prive"
        class="btn-Submi"
        type="primary"
        size="small"
        color="#1989fa"
        @click="recharge"
      >下一步</van-button>
    </div>

    <van-popup v-model="show" @click-overlay="blur">
      <img style="width:200px" src="../../assets/img/wx.jpg" alt />
    </van-popup>

    <van-number-keyboard
      :show="active"
      extra-key="."
      close-button-text="完成"
      @blur="active = false"
      @input="onInput"
      @delete="onDelete"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      prive: "",
      show: false,
      active: false
    };
  },
  methods: {
    recharge() {
      this.show = true;
      this.$store.state.balance += parseInt(this.prive);
    },
    onInput(value) {
      this.prive +=value
    },
    onDelete() {
    this.prive=this.prive.substr(0,  this.prive.length - 1)
      // console.log("删除");
    },
    prohibit(){
        return false
    },
    blur(){
        this.$router.go(-1)
    }
  }
};
</script>

<style lang="scss" scoped>
.bg {
  background: #eee;
  height: 100vh;
}
.box {
  padding: 0 24px;
  background: #fff;
  .prive {
    padding: 10px 0;
    height: 50px;
    span {
      font-size: 18px;
      font-weight: 100;
      line-height: 50px;
    }
    input {
      border: none;
      font-size: 22px;
      height: 100%;
    }
  }
}
h3 {
  margin: 0;
  font-size: 14px;
  color: #666;
}
.box-btn {
  padding: 0 24px;
  margin-top: 24px;
}
.btn-Submi {
  width: 100%;
}
</style>